"use client"
import React, { useEffect, useState } from 'react'
import API from '@/lib/api'
import Link from 'next/link'

export default function DashboardPage() {
  const [stats, setStats] = useState<any>(null)

  useEffect(() => {
    const token = localStorage.getItem('token')
    if (!token) return
    // Fetch user's groups and events
    API.get('/groups', { headers: { Authorization: `Bearer ${token}` } })
      .then((r) => setStats({ groups: r.data.groups }))
      .catch(() => null)
  }, [])

  return (
    <div className="mx-auto max-w-4xl p-6">
      <h2 className="text-2xl font-semibold text-white">Organizer Dashboard</h2>
      <p className="mt-2 text-sm text-zinc-300">Manage your groups and events</p>
      
      <div className="mt-6 grid grid-cols-1 gap-4 sm:grid-cols-2">
        <div className="rounded-xl bg-white/10 p-6 backdrop-blur-md">
          <h3 className="text-lg font-semibold text-white">Your Groups</h3>
          <p className="mt-2 text-3xl font-bold text-pink-500">{stats?.groups?.length || 0}</p>
          <Link href="/groups/create" className="mt-4 inline-block rounded bg-pink-500 px-4 py-2 text-sm text-white hover:bg-pink-600">
            Create Group
          </Link>
        </div>
        <div className="rounded-xl bg-white/10 p-6 backdrop-blur-md">
          <h3 className="text-lg font-semibold text-white">Total Events</h3>
          <p className="mt-2 text-3xl font-bold text-pink-500">0</p>
        </div>
      </div>
    </div>
  )
}
